body {
  overflow: hidden;
  -webkit-tap-highlight-color: rgba(18, 150, 219, .2);
}

#root {
  height: 100vh;
}

.game-board-wrapper {
  font-family: arial;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);

  --block-unit: 60px;
  --gap-width: 6px;
  --row-gap: 8px;
  --board-width: calc(4 * var(--block-unit) + 3 * var(--gap-width));
}

.board {
  display: block;
  position: relative;
  margin: 10px auto;
  border: 1px solid #ccc;
  width: var(--board-width);
  height: var(--board-width);
  padding: var(--gap-width);
  border-radius: 4px;
}

.board span {
  letter-spacing: -1px;
  display: block;
  width: var(--block-unit);
  height: var(--block-unit);
  line-height: var(--block-unit);
  position: absolute;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: calc(var(--block-unit) / 5 * 2);
  background-color: #ebe76f;
  border-radius: 5px;
  -webkit-transition: all 100ms linear;
  -o-transition: all 100ms linear;
  transition: all 100ms linear;
}

.welcome-image {
  margin-bottom: calc(var(--row-gap) * 3);
  max-height: 100px;
}

.header {
  display: -ms-flexbox;
  display: flex;
  width: calc( var(--board-width) + 2 * var(--gap-width));
  margin-bottom: var(--row-gap);
}

.header > .header-image {
  -ms-flex: 1 1;
      flex: 1 1;
}

.header > .actions {
  -ms-flex: 1 1;
      flex: 1 1;
  padding-left: 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.actions > .clickable {
  padding: 8px 10px;
  border-radius: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
  background-color: white;
  -webkit-box-shadow: 0 0 0 1px #eee;
          box-shadow: 0 0 0 1px #eee;
  min-height: 24px;
  max-height: 50px;
  font-size: 15px;
}

.actions > .score > .prefix {
  font-size: 14px;
}

.actions > .score > .content {
  font-size: 18px;
  font-weight: bold;
  min-width: 40px;
  text-align: center;
}

.actions > .new-game {
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: rgba(51, 51, 51, 1);
  color: white;
}

.game-board-wrapper .actions > .clickable {
  margin: 4px 0;
}

.game-over {
  position: absolute;
  left: 10%;
  right: 10%;
  top: 0;
  bottom: 0;
  margin: auto 0;
  border-radius: 4px;
  background-color: rgba(51, 51, 51, 0.8);
  height: calc(var(--board-width) / 3);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  font-size: 30px;
  white-space: nowrap;
  font-weight: bold;
  z-index: 20;
}

.win-tips-wrapper, .activity-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgba(51, 51, 51, 0.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.win-tips-wrapper .clickable {
  margin-top: 20px;
  background: #F0C21F;
  font-weight: bold;
  font-size: 18px;
  color: white;
  min-height: 36px;
  -webkit-box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
          box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
}

.text-stroke {
  color: rgba(255, 199, 0, 1);
  font-size: 30px;
  font-weight: bold;
  --stroke-width: .025em;
  --reverse-stroke-width: calc(0px - var(--stroke-width));
  text-shadow: 
				var(--reverse-stroke-width) var(--reverse-stroke-width) 0 black, 
				var(--stroke-width) var(--reverse-stroke-width) 0 black, 
				var(--reverse-stroke-width) var(--stroke-width) 0 black, 
				var(--stroke-width) var(--stroke-width) 0 black;
}

.text-stroke.mini {
  font-size: 18px;
  --stroke-width: 1px;
}


.activity-wrapper, .activity-wrapper * {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.activity-wrapper {
  padding: 50px 20px;
  overflow-y: auto;
  overflow-x: hidden;
  height: 100vh;
  bottom: unset;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.activity-wrapper > .first-line {
  position: relative;
  overflow: visible;
  text-align: center;
}

.first-line > .cursor-left, .first-line > .cursor-right {
  position: absolute;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  width: 54px;
  height: 54px;
  top: -10px;
}

.first-line > .cursor-left {
  left: -60px;
}

.first-line > .cursor-right {
  right: -60px;
  -webkit-transform: scaleX(-1) ;
      -ms-transform: scaleX(-1) ;
          transform: scaleX(-1) ;
}

.activity-wrapper > .gift, .activity-wrapper > .qrcode {
  width: 60vw;
  height: 60vw;
  max-width: 200px;
  max-height: 200px;
  border-radius: 4px;
  margin: 20px 0;
}

.activity-wrapper > .content {
  text-align: center;
  padding: 0 5px;
}

.activity-wrapper > .actions {
  margin-top: 20px;
}

.activity-cursor {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 4px 0;
  overflow: visible;
  cursor: pointer;
}

.plate-wrapper {
  position: relative;
  width: 32px;
  height: 32px;
  overflow: visible;
  display: inline-block;
  margin-left: 4px;
}

.plate-wrapper > .background {
  width: 74px;
  height: 74px;
  position: absolute;
  left: -37px;
  right: -37px;
  bottom: -12px;
  margin: 0 auto;
  z-index: 1;
  -webkit-animation: rotate 2s linear infinite;
          animation: rotate 2s linear infinite;
}

.plate-wrapper > .plate {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 32px;
  height: 32px;
  z-index: 2;
}

@-webkit-keyframes rotate {
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.a1,
.b1,
.c1,
.d1 {
  left: var(--gap-width);
}
.a2,
.b2,
.c2,
.d2 {
  left: calc(var(--gap-width) * 2 + var(--block-unit));
}
.a3,
.b3,
.c3,
.d3 {
  left: calc(var(--gap-width) * 3 + var(--block-unit) * 2);
}
.a4,
.b4,
.c4,
.d4 {
  left: calc(var(--gap-width) * 4 + var(--block-unit) * 3);
}
.a1,
.a2,
.a3,
.a4 {
  top: var(--gap-width);
}
.b1,
.b2,
.b3,
.b4 {
  top: calc(var(--gap-width) * 2 + var(--block-unit));
}
.c1,
.c2,
.c3,
.c4 {
  top: calc(var(--gap-width) * 3 + var(--block-unit) * 2);
}
.d1,
.d2,
.d3,
.d4 {
  top: calc(var(--gap-width) * 4 + var(--block-unit) * 3);
}
span.value2 {
  background-color: #ebb26f;
}
span.value4 {
  background-color: #ea6feb;
}
span.value8 {
  background-color: #eb6fa3;
}
span.value16 {
  background-color: #7a6feb;
}
span.value32 {
  background-color: #af6feb;
}
span.value64 {
  background-color: #6febcf;
}
span.value128 {
  background-color: #6fbeeb;
}
span.value256 {
  background-color: #afeb6f;
}
span.value512 {
  background-color: #7aeb6f;
}
span.value1024 {
