  font-size: 18px;
}


@media (min-width: 800px) {
  .game-board-wrapper {
    --block-unit: 80px;
    --gap-width: 8px;
    --row-gap: 15px;
  }
  .welcome-image {
    height: 15vh;
    max-height: 200px;
  }
  .actions > .clickable {
    font-size: 18px;
    min-height: 32px;
  }

  .win-tips-wrapper > .win-tips {
    font-size: 50px;
  }

  .win-tips-wrapper .clickable {
    font-size: 30px;
    padding: 20px;
    margin-top: 40px;
  }
}

@media (min-height: 800px) {
  .activity-wrapper {
    bottom: 0;
    -ms-flex-pack: center;
        justify-content: center;
  }
}

@supports (-webkit-text-stroke: 1px black) {
  .text-stroke {
    --stroke-width: 1.5px;
    text-shadow: none;
    -webkit-text-stroke: var(--stroke-width) black;
    font-weight: 800;
  }
  .text-stroke.mini {
    --stroke-width: .025em;
  }
}


